<!-- setting -->
<template>
  <v-tab-item
    key="setting"
    class="tab-setting-container"
  >
    <v-card elevation="2">
      <v-card-title>模型运行参数</v-card-title>
      <v-divider />
      <v-card-text>
        <br>
        <v-form>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <div
                v-bind="attrs"
                style="width: 100%;"
                v-on="on"
              >
                <v-slider
                  v-model="max_length"
                  :min="2000"
                  :max="8192"
                  :step="100"
                  :thumb-color="color"
                  label="max_length"
                  thumb-label
                  @change="(v)=>$emit('max_length-changed',v)"
                >
                  <template v-slot:append>
                    <v-text-field
                      v-model="max_length"
                      class="mt-0 pt-0"
                      type="number"
                      style="width: 60px"
                      @change="(v)=>$emit('max_length-changed',v)"
                    />
                  </template>
                </v-slider>
              </div>
            </template>
            <span>最大生成token数（注意，不同模型对此参数实现不同。部分会将输入token计入）</span>
          </v-tooltip>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <div
                v-bind="attrs"
                style="width: 100%;"
                v-on="on"
              >
                <v-slider
                  v-model="temperature"
                  :min="0"
                  :max="2"
                  :step="0.1"
                  :thumb-color="color"
                  label="temperature"
                  thumb-label
                  @change="(v)=>$emit('temperature-changed',v)"
                >
                  <template
                    v-slot:append
                  >
                    <v-text-field
                      v-model="temperature"
                      class="mt-0 pt-0"
                      type="number"
                      style="width: 60px"
                      @change="(v)=>$emit('temperature-changed',v)"
                    />
                  </template>
                </v-slider>
              </div>
            </template>
            <span>温度（随机性）</span>
          </v-tooltip>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <div
                v-bind="attrs"
                style="width: 100%;"
                v-on="on"
              >
                <v-slider
                  v-model="top_p"
                  :min="0"
                  :max="1"
                  :step="0.1"
                  :thumb-color="color"
                  label="top_p"
                  thumb-label
                  @change="(v)=>$emit('top_p-changed',v)"
                >
                  <template v-slot:append>
                    <v-text-field
                      v-model="top_p"
                      class="mt-0 pt-0"
                      type="number"
                      style="width: 60px"
                      @change="(v)=>$emit('top_p-changed',v)"
                    />
                  </template>
                </v-slider>
              </div>
            </template>
            <span>选取的前p个输出数量</span>
          </v-tooltip>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <div
                v-bind="attrs"
                style="width: 100%;"
                v-on="on"
              >
                <v-slider
                  v-model="cfg_factor"
                  :min="0"
                  :max="2"
                  :step="0.1"
                  :thumb-color="color"
                  label="cfg_factor"
                  thumb-label
                  @change="(v)=>$emit('cfg_factor-changed',v)"
                >
                  <template v-slot:append>
                    <v-text-field
                      v-model="cfg_factor"
                      class="mt-0 pt-0"
                      type="number"
                      style="width: 60px"
                      @change="(v)=>$emit('cfg_factor-changed',v)"
                    />
                  </template>
                </v-slider>
              </div>
            </template>
            <span>classifier free guidence 因子，当前仅支持rwkv</span>
          </v-tooltip>
          <v-row>
            <v-col
              cols="12"
              sm="2"
            >
              <v-switch
                v-model="simplify_historical_information"
                hide-details
                :label="$t('simplify_historical_information')"
                :color="color"
                inset
                @change="(v)=>$emit('simplify_historical_information-changed',v)"
              />
            </v-col>
            <v-col
              cols="12"
              sm="10"
            >
              <v-slider
                v-model="history_limit"
                :min="0"
                :max="10"
                :step="1"
                :thumb-color="color"
                label="对话轮数限制"
                thumb-label
                @change="(v)=>$emit('history_limit-changed',v)"
              >
                <template v-slot:append>
                  <v-text-field
                    v-model="history_limit"
                    class="mt-0 pt-0"
                    type="number"
                    style="width: 60px"
                    @change="(v)=>$emit('history_limit-changed',v)"
                  />
                </template>
              </v-slider>
            </v-col>
          </v-row>
        </v-form>
      </v-card-text>
    </v-card>
    <v-card elevation="2">
      <v-card-title>动态调整知识库</v-card-title>
      <v-divider />
      <v-card-text>
        网页刷新后恢复<br>
        <v-row>
          <v-col cols="8">
            <v-combobox
              v-model="zsk_libraryStategy"
              :items="['rtst:3:default agents:0','sogowx:3 bing:3']"
              label="库参数"
            />
          </v-col>
          <v-col cols="4">
            <v-text-field
              v-model="zsk_maxItmes"
              outlined
              label="最大数量"
            />
          </v-col>
        </v-row>
      </v-card-text>
      <v-card-actions>
        <v-btn
          :color="color"
          dark
          size="x-large"
          @click="onLibStategyChanged"
        >
          设置
        </v-btn>
      </v-card-actions>
    </v-card>
    <v-card elevation="2">
      <v-card-title>知识提取提示词</v-card-title>
      <v-divider />
      <v-card-text>
        <br>
        <v-combobox
          v-model="zsk_summarize_prompt"
          :items="['总结以下文段中与问题相关的信息。']"
          label="总结提示词"
          @change="(v)=>$emit('zsk_summarize_prompt-changed',v)"
        />
        <v-combobox
          v-model="zsk_answer_prompt"
          :items="['学习以下文段,用中文回答问题。如果无法从中得到答案，忽略文段内容并用中文回答问题。']"
          label="回答提示词"
          @change="(v)=>$emit('zsk_answer_prompt-changed',v)"
        />
      </v-card-text>
    </v-card>
    <v-card elevation="2">
      <v-card-title>查询知识库</v-card-title>
      <v-divider /><br>
      <v-card-text>
        <v-row>
          <v-col
            cols="12"
            sm="6"
          >
            <v-text-field
              v-model="test_zsk_prompt"
              outlined
              label="关键词"
              clearable
              @change="(v)=>$emit('test_zsk_prompt-changed',v)"
            />
          </v-col>
          <v-col
            cols="12"
            sm="6"
          >
            <v-text-field
              v-model="zsk_step"
              outlined
              label="上下文数量"
              clearable
              @change="(v)=>$emit('zsk_step-changed',v)"
            />
          </v-col>
        </v-row>
        <v-data-table
          :headers="[{ text: '来源', value: 'title' },{ text: '分数', value: 'score' },{ text: '内容', value: 'content' }]"
          :items="zhishiku"
          hide-default-header
          hide-default-footer
        >
          <template v-slot:[`item.title`]="{ item }">
            <p v-html="$util.md2html(item.title)" />
          </template>
        </v-data-table>
        <v-card-actions>
          <v-btn
            :color="color"
            dark
            size="x-large"
            @click="window.find(test_zsk_prompt,zsk_step)"
          >
            测试
          </v-btn>
        </v-card-actions>
      </v-card-text>
    </v-card>
  </v-tab-item>
</template>

<script>
export default {
  name: 'TabItemSetting',
  props: {
    maxLength: {
      type: [Number, String],
      default: 4096
    },
    propTemperature: {
      type: Number,
      default: 0.8
    },
    topP: {
      type: Number,
      default: 0.8
    },
    cfgFactor: {
      type: Number,
      default: 1
    },
    simplifyHistoricalInformation: {
      type: Boolean,
      default: false
    },
    historyLimit: {
      type: Number,
      default: 5
    },
    defaultColor: {
      type: String,
      default: '#0077F1'
    },
    zskLibraryStategy: {
      type: String,
      default: ''
    },
    zskMaxItmes: {
      type: Number,
      default: 5
    },
    zskSummarizePrompt: {
      type: String,
      default: '总结以下文段中与问题相关的信息。'
    },
    zskAnswerPrompt: {
      type: String,
      default: '学习以下文段,用中文回答问题。'
    },
    testZskPrompt: {
      type: String,
      default: ''
    },
    zskStep: {
      type: Number,
      default: 2
    },
    zhishiku: {
      type: Array,
      default: () => { return [] }
    }
  },
  data() {
    return {
      window: window,
      zsk_libraryStategy: '',
      zsk_maxItmes: 5
    }
  },
  computed: {
    max_length: {
      get() {
        return this.maxLength
      },
      set(v) {
        this.$emit('max_length-changed', v)
      }
    },
    temperature: {
      get() {
        return this.propTemperature
      },
      set(v) {
        this.$emit('temperature-changed', v)
      }
    },
    top_p: {
      get() {
        return this.topP
      },
      set(v) {
        this.$emit('top_p-changed', v)
      }
    },
    cfg_factor: {
      get() {
        return this.cfgFactor
      },
      set(v) {
        this.$emit('cfg_factor-changed', v)
      }
    },
    zsk_summarize_prompt: {
      get() {
        return this.zskSummarizePrompt
      },
      set(v) {
        this.$emit('zsk_summarize_prompt-changed', v)
      }
    },
    zsk_answer_prompt: {
      get() {
        return this.zskAnswerPrompt
      },
      set(v) {
        this.$emit('zsk_answer_prompt-changed', v)
      }
    },
    test_zsk_prompt: {
      get() {
        return this.testZskPrompt
      },
      set(v) {
        this.$emit('test_zsk_prompt-changed', v)
      }
    },
    zsk_step: {
      get() {
        return this.zskStep
      },
      set(v) {
        this.$emit('zsk_step-changed', v)
      }
    },
    simplify_historical_information: {
      get() {
        return this.simplifyHistoricalInformation
      },
      set(v) {
        this.$emit('simplify_historical_information-changed', v)
      }
    },
    history_limit: {
      get() {
        return this.historyLimit
      },
      set(v) {
        this.$emit('history_limit-changed', v)
      }
    },
    color() {
      return this.defaultColor
    }
  },
  created() {
    this.zsk_libraryStategy = this.zskLibraryStategy
    this.zsk_maxItmes = this.zskMaxItmes
  },
  methods: {
    onLibStategyChanged() {
      this.$emit('libstategy-changed', this.zsk_libraryStategy, this.zsk_maxItmes)
    }
  }
}
</script>

<style lang='scss' scoped>
</style>
