<!-- chat -->
<template>
  <v-tab-item
    key="chat"
    style="padding-bottom:136px"
  >
    <!-- 金小税相关 -->
    <div
      v-if="func_mode.name === '金小税'"
      :class="['d-flex flex-row align-center my-2']"
    >
      <v-avatar
        size="40"
      >
        <v-icon size="40">
          $vuetify.icons.robot_face
        </v-icon>
      </v-avatar>
      <span
        class="answer"
      >
        您好，很高兴为您服务，请问有什么可以帮助您？
      </span>
    </div>
    <div
      v-if="func_mode.name === '金小税'"
      class="golden-tax-faqs"
    >
      <p class="mdi-question">
        <span
          class="mdi mdi-chat-question"
          style="color:#007aff; margin-right:16px;font-size:28px;"
        /><span style="line-height: 32px;vertical-align: 6px;">猜你想问</span>
      </p>
      <ul>
        <li
          v-for="(faq,idx) in window.app.golden_tax_faqs"
          :key="idx"
          @click="askFaq(faq)"
        >
          {{ faq }}
        </li>
      </ul>
    </div>
    <!-- 对话区 -->
    <div
      v-for="(current_conversation, index) in chatList"
      :key="index"
      :class="['chart-wrapper d-flex flex-row align-center my-2', current_conversation.role == 'user' ? 'justify-end': null]"
    >
      <v-hover v-slot="{ hover }">
        <div
          v-if="current_conversation.role === 'user'"
          class="ask"
        >
          <span
            autofocus
            v-text="current_conversation.keyword||current_conversation.content"
          />

          <div
            v-if="hover"
            class="float"
          >
            <v-icon
              v-if="!isLoading"
              text
              @click="window.delete_current_conversation(current_conversation)"
            >
              $btn_delete
            </v-icon>

            <v-icon
              text
              @click="window.copy(current_conversation.content)"
            >
              $btn_copy
            </v-icon>
            <v-icon
              text
              @click="window.edit(current_conversation)"
            >
              $btn_edit
            </v-icon>
          </div>
        </div>
      </v-hover>
      <!-- <v-avatar
        :class="current_conversation.role === 'user' ? 'user-avatar': 'bot-avatar'"
        size="80"
        class="头像"
      > -->
      <!-- <span>{{ {user:"问",AI:'答'}[current_conversation.role] }}</span> -->
      <div class="chat-avatar">
        <v-icon
          v-if="current_conversation.role === 'user'"
        >
          $vuetify.icons.person_face
        </v-icon>
        <v-icon
          v-else
        >
          $vuetify.icons.robot_face
        </v-icon>
      </div>
      <!-- </v-avatar> -->
      <v-hover v-slot="{ hover }">
        <div
          v-if="current_conversation.role !== 'user'"
          class="answer"
        >
          <span>
            <div v-html="md2html(current_conversation.content)" />
            <div
              v-if="current_conversation.sources && current_conversation.sources.length>0"
              class="mt-2 pt-4"
              style="border-top:1px dashed #999;"
            >
              <span style="font-size:14px;">相关材料：</span>
              <v-tooltip
                v-for="(source,idx) in current_conversation.sources"
                :key="idx"
                bottom
                style="margin-top:10px;"
                :max-width="500"
              >
                <template v-slot:activator="{ on, attrs }">
                  <div
                    v-bind="attrs"
                    style="margin: 3px;"
                    v-on="on"
                  >
                    <v-btn
                      outlined
                      style="
                      margin-top: 0px;
                      max-width: -webkit-fill-available;
                      overflow-x: hidden;
                    "
                      color="#0077F1"
                      rounded
                      small
                      @click="source.url?window.open(source.url,&quot;_blank&quot;):source.click()"
                    >
                      <span
                        class="d-inline-block text-truncate"
                        style="max-width: 150px"
                      >
                        {{ source.title }}
                      </span>
                    </v-btn>
                  </div>
                </template>
                <span v-html="source.content" />
              </v-tooltip>
            </div>
          </span>
          <div
            v-if="hover"
            class="float"
          >
            <v-icon
              v-if="!isLoading"
              text
              @click="window.delete_current_conversation(current_conversation)"
            >
              $btn_delete
            </v-icon>

            <v-icon
              text
              @click="window.copy(current_conversation.content)"
            >
              $btn_copy
            </v-icon>
            <v-icon
              text
              @click="window.edit(current_conversation)"
            >
              $btn_edit
            </v-icon>
          </div>
        </div>
      </v-hover>
    </div>
    <!-- <div v-if="!isLoading&&chatList.length>0">
      <v-tooltip left>
        <template v-slot:activator="{ on, attrs }">
          <v-icon
            class="float"
            large
            text
            style="right: 0;"
            v-bind="attrs"
            v-on="on"
            @click="window.re_generate"
          >
            mdi-refresh
          </v-icon>
        </template>
        <span>{{ $t("re_generate") }}</span>
      </v-tooltip>
    </div> -->
    <!-- <br><br><br><br><br><br><br><br><br> -->
    <!-- <v-footer fixed>
      <div
        v-if="语音输入"
        style="position: fixed;bottom: 120px;width: 100%;z-index: 20;"
      >
        <v-btn
          v-if="!sst_started"
          style="margin: auto;display: flex;"
          color="#0077F1"
          fab
          width="100"
          height="100"
          dark
          @click="()=>{ switchSiriWave(true); window.listen() }"
        >
          <v-icon size="54">
            mdi-microphone
          </v-icon>
        </v-btn>
        <v-btn
          v-if="sst_started"
          style="margin: auto;display:flex;z-index:999;background:transparent;"
          :color="isLoading ? 'rgb(0, 78, 162, 0.3)':'transparent'"
          width="100"
          height="100"
          depressed
          plain
          icon
          light
          :loading="isLoading"
          @click="()=>{ switchSiriWave(false); window.stop_listen() }"
        >
          <v-icon
            color="red"
            size="54"
          >
            mdi-microphone-off
          </v-icon>
        </v-btn>
        <div
          v-show="sst_started && !isLoading"
          id="siri-container"
        />
      </div>
      <v-textarea
        v-model="input_question"
        autofocus
        hide-details
        no-resize
        rows="3"
        :loading="isLoading"
        :placeholder="$t(func_mode.description)"
        @keypress.enter="submitQuestion"
      />
      <div>
        <v-btn
          v-if="isLoading"
          style="margin-bottom: 10px;margin-top: 0;"
          color="#004EA2"
          rounded
          dark
          size="x-large"
          @click="abort_chatting"
        >
          {{ $t("abord") }}
        </v-btn>
        <v-btn
          v-if="!isLoading"
          style="margin-bottom: 10px;margin-top: 0; border-radius: 26px;"
          dark
          color="#004EA2"
          rounded
          large
          @click="submitQuestion"
        >
          {{ $t("send") }}
        </v-btn>
        <br>
        <v-expand-transition>
          <v-chip
            v-if="func_mode.name!=''"
            color="#004EA2"
            outlined
            close
            @click:close="$emit('reset-mode')"
          >
            {{ func_mode.name }}
          </v-chip>
        </v-expand-transition>
      </div>
    </v-footer> -->
    <v-footer
      fixed
      class="pr-0"
    >
      <div class="chat_toolbar">
        <v-tooltip
          v-for="(button,idx) in buttons"
          :key="idx"
          bottom
        >
          <template v-slot:activator="{ on, attrs }">
            <div
              v-bind="attrs"
              v-on="on"
            >
              <v-icon
                v-if="toolbarIconsInner.includes(button.icon)"
                :color="button.color()"
                text
                @click="button.click"
              >
                $vuetify.icons.{{ button.icon }}
              </v-icon>
              <v-icon
                v-else
                :color="button.color()"
                text
                @click="button.click"
              >
                mdi-{{ button.icon }}
              </v-icon>
            </div>
          </template>
          <span>{{ button.description }}</span>
        </v-tooltip>
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <div
              v-bind="attrs"
              v-on="on"
            >
              <v-icon
                :style="{fill:current_func=='知识库'?color:'#000'}"
                @click="$emit('crntfunc-changed',current_func=='知识库'?'':'知识库')"
              >
                <!-- mdi-book-open-variant -->
                $vuetify.icons.tb_book
              </v-icon>
            </div>
          </template>
          <span>{{ $t("zhishiku") }}</span>
        </v-tooltip>
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <div
              v-bind="attrs"
              v-on="on"
            >
              <v-icon
                :style="{fill:current_func=='快速知识库'?color:'#000'}"
                text
                @click="$emit('crntfunc-changed',current_func=='快速知识库'?'':'快速知识库')"
              >
                $tb_thunder
              </v-icon>
            </div>
          </template>
          <span>{{ $t("zhishiku") }}快速模式</span>
        </v-tooltip>
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <div
              v-bind="attrs"
              v-on="on"
            >
              <v-icon
                :style="{fill:sst_started?color:''}"
                text
                @click="sst_started?window.stop_listen():window.listen()"
              >
                $tb_mic
              </v-icon>
            </div>
          </template>
          <span>语音输入</span>
        </v-tooltip>
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <div
              v-bind="attrs"
              v-on="on"
            >
              <v-icon
                :style="{fill:tts_on?color:''}"
                text
                @click="$emit('toggle-ttson')"
              >
                $tb_music
              </v-icon>
            </div>
          </template>
          <span>阅读文本</span>
        </v-tooltip>
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <div
              v-bind="attrs"
              v-on="on"
            >
              <v-icon
                :style="{fill:history_on?color:''}"
                text
                @click="$emit('toggle-historyon')"
              >
                $tb_history
              </v-icon>
            </div>
          </template>
          <span>{{ $t("history") }}</span>
        </v-tooltip>
        <v-tooltip
          v-if="!isLoading&&chatList.length>0"
          bottom
        >
          <template v-slot:activator="{ on, attrs }">
            <div
              v-bind="attrs"
              v-on="on"
            >
              <v-icon
                :style="{fill:color}"
                v-bind="attrs"
                v-on="on"
                @click="window.re_generate()"
              >
                $tb_refresh
              </v-icon>
            </div>
          </template>
          <span>{{ $t("re_generate") }}</span>
        </v-tooltip>
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <div
              v-bind="attrs"
              v-on="on"
            >
              <v-icon
                :style="{fill:color}"
                text
                @click="$emit('clear-history')"
              >
                $tb_clear
              </v-icon>
            </div>
          </template>
          <span>{{ $t("clear_history") }}</span>
        </v-tooltip>
        <b>TPS: {{ TPS }} </b>
      </div>
      <v-row
        no-gutters
        class="mr-5 mb-4 chat-box"
        style="border:1px solid rgba(0,119,241,0.30);border-radius:20px; background: rgba(0,119,241,0.03);"
      >
        <v-col
          cols="8"
          md="11"
        >
          <v-textarea
            ref="inputBox"
            v-model="input_question"
            class="pa-2 pb-0"
            hide-details
            auto-grow
            no-resize
            rows="2"
            :placeholder="$t(func_mode.description)"
            @keypress.enter="submitQuestion"
          />
        </v-col>
        <v-col
          cols="4"
          md="1"
        >
          <div
            class="d-flex flex-column align-end justify-end py-4"
            style="height:100%; over"
          >
            <!-- <v-icon
              :color="color"
              class="mr-4"
              v-bind="attrs"
              size="36"
              v-on="on"
              $tb_link
            </v-icon> -->
            <v-icon
              v-if="isLoading"
              class="mr-4"
              :color="color"
              text
              @click="window.abort_chatting()"
            >
              $vuetify.icons.stop_send
            </v-icon>
            <v-icon
              v-if="!isLoading"
              class="mr-4"
              :color="color"
              text
              @click="submitQuestion"
            >
              $vuetify.icons.send_prompt
            </v-icon>
            <v-expand-transition>
              <v-chip
                v-if="func_mode.name!=''"
                class="mr-4 mt-2"
                :color="color"
                outlined
                close
                @click:close="$emit('crntfunc-changed', '')"
              >
                <span
                  class="d-inline-block text-truncate"
                  style="max-width: 120px"
                >
                  {{ func_mode.name }}
                </span>
              </v-chip>
            </v-expand-transition>
          </div>
        </v-col>
      </v-row>
    </v-footer>
  </v-tab-item>
</template>

<script>
import SiriWave from 'siriwave'
import markdownit from 'markdown-it'
export default {
  name: 'TabItemChat',
  props: {
    chats: {
      type: Array,
      default: () => []
    },
    crntFunc: {
      type: String,
      default: ''
    },
    defaultColor: {
      type: String,
      default: '#0077F1'
    },
    buttons: {
      type: Array,
      default: () => []
    },
    loading: {
      type: Boolean,
      default: false
    },
    question: {
      type: String,
      default: ''
    },
    voiceInputing: {
      type: Boolean,
      default: false
    },
    ttson: {
      type: Boolean,
      default: false
    },
    historyon: {
      type: Boolean,
      default: false
    },
    voiceEnable: {
      type: Boolean,
      default: false
    },
    conversitionMode: {
      type: Object,
      default: null
    },
    tps: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      window: window,
      chatList: [],
      input_question: '',
      func_mode: {
        name: '',
        description: 'input_question',
        question: ''
      },
      waveIndicator: null,
      toolbarIconsInner: ['thumb-up', 'account-outline']
    }
  },
  computed: {
    color() {
      return this.defaultColor
    },
    current_func() {
      return this.$props.crntFunc
    },
    isLoading: {
      get() {
        return this.loading
      },
      set(v) {}
    },
    TPS: {
      get() {
        return this.tps.toFixed(2)
      },
      set(v) {}
    },
    语音输入: {
      get() {
        return this.voiceEnable
      },
      set(v) {}
    },
    sst_started: {
      get() {
        return this.voiceInputing
      },
      set(v) {}
    },
    tts_on() {
      return this.ttson
    },
    history_on() {
      return this.historyon
    }
  },
  watch: {
    '$props.conversitionMode': {
      handler(v) {
        this.func_mode = { ...v }
      },
      deep: true
    },
    '$props.question': {
      handler(v) {
        this.input_question = v
      }
    },
    '$props.chats': {
      handler(v) {
        this.chatList = [...v]
      },
      deep: true
    }
  },
  created() {
    this.chatList = this.chats
    this.input_question = this.question
    this.语音输入 = this.voiceEnable
    this.sst_started = this.voiceInputing
    this.func_mode = { ...this.conversitionMode }
  },
  methods: {
    askFaq(v) {
      if (this.isLoading) {
        return
      }
      this.$emit('question-changed', v)
    },
    md2html: (conent) => {
      conent = String(conent)
      const md = new markdownit()
      // md.disable(['link', 'image'])
      return md.render(conent).replace(/<a /g, '<a target="_blank"').replace(/[\r\n]+!$/g, '<br>')
    },
    submitQuestion($event) {
      if ($event && $event.type === 'keypress' && $event.shiftKey) {
        return
      } else if ($event && $event.type === 'keypress') {
        $event.preventDefault()
      }
      if (this.isLoading) {
        return
      }
      if (this.input_question.trim() === '') {
        return
      }
      this.$emit('question-changed', this.input_question)
      this.input_question = ''
    },
    switchSiriWave(isShow) {
      if (isShow) {
        if (!this.waveIndicator) {
          this.waveIndicator = new SiriWave({
            container: document.querySelector('#siri-container'),
            color: '#4caf50',
            width: 200,
            height: 30,
            speed: 0.1,
            amplitude: 1
          })
        } else {
          this.waveIndicator.start()
        }
      } else {
        this.waveIndicator.stop()
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.golden-tax-faqs{
  padding: 24px;
  margin-left: 80px;
  width: 36%;
  border: 1px solid rgba(0,119,241,0.30);
  box-shadow: 0 0 4px 0 rgba(188,197,212,.4);
  border-radius: 8px;
  ul{
    padding:0;
    margin:0;
    width:100%;
    li{
      margin-left: 24px;
      line-height: 36px;
      color:#1768f3;
      cursor:pointer;
      border-bottom: 1px solid #edf0f6;
    }
  }
}

.v-footer.v-sheet.theme--light.v-footer--fixed{
  background: #fff;
  border-radius: 20px;
}
#siri-container{
    position: absolute;
    top: 48px;
    left: calc(50% - 100px);
    width: 200;
    background: transparent;
    margin: auto;
    display: flex;
}

.chat_toolbar {
  width: 100%;
  padding-left:20px;
  color: #d0d0d0;
}

.chat_toolbar .v-input,
.chat_toolbar div[aria-haspopup] {
  display: inline-block;
  margin: 12px;
}

.v-footer::v-deep .v-textarea textarea{
  line-height: 24px;
  padding-left:20px;
  overflow-y:auto;
  max-height: calc(36vh - 62px);
  &::-webkit-scrollbar{
    width:0;
  }
}

.v-text-field.v-textarea>.v-input__control>.v-input__slot:before{
  width: 0;
}

</style>

